<template>
    <div>
        <Breadcrumbs title="User Management"/>
        <loading
        loader="dots"
        :active.sync="isloading"
        :can-cancel="false"
        :is-full-page="true"
        ></loading>
        <!-- Container-fluid starts-->
        <div class="container-fluid">
            <div class="card">
                <div class="card-header">
                    <h5></h5><span></span>
                </div>
                <div class="card-body">
                    <b-table hover striped :fields="fields" :items="user_list" responsive='sm'>
                        <template v-if="userinfo.userrole=='sa'" #cell(user_role)="data">
                            <span v-if="data.value == 'sa'">Super Admin</span>
                            <b-form-select v-else v-model="data.item.user_role" :options="options" class="w-75"></b-form-select>
                        </template>
                    </b-table>
                    <b-button v-if="userinfo.userrole=='sa'" class="btn btn-success btn-ms m-1" @click="save_change()"><i class="fa fa-pencil"></i></b-button>
                </div>
            </div>
            </div>               
        <!-- Container-fluid Ends-->
    </div>
</template>

<script>
import {api_server} from '@/main'
export default {
    data(){
        return{
        isloading: false,
        userinfo: JSON.parse(window.sessionStorage.getItem('userInfo')),
        options: [{value: 'user', text: 'user'},
                  {value: 'admin', text: 'admin'}],
        fields:[{key:'id', label:'ID'},
                {key: 'username', label:''},
                {key: 'email', label: ''},
                {key: 'password', label: ''},
                {key: 'user_role', label: ''},
                {key: 'created', label: ''},
                ],
        user_list: [
    ]
        }
    },
    created(){
        this.get_user_list()
    },
    methods:{
        role_change(data){
            console.log(data)
            // this.userinfo[data.index] = data.value
        },
        get_user_list(){
            this.isloading = true
            api_server.get('/admin/user_management')
            .then(res=>{
                this.user_list = res.data.data
            }).catch(err=>{
                console.log(err.response)
                this.$swal({
                    title: '',
                    type: 'error',
                    text:'!'
                })
            }).finally(()=>{
                this.isloading = false
            })
        },
        save_change(){
            this.$swal({
                title: "?",
                type: 'info',
                showCancelButton: true,
                cancelButtonText: '',
                confirmButtonText: ''
            }).then(result=>{
                if (result.value){
                    this.isloading = true
                    api_server.post('/admin/user_management', {data: this.user_list})
                    .then(res=>{
                        this.$swal({
                            title: '',
                            type: 'success',
                            text: ` ${res.data.data} `
                        })
                        this.get_user_list()
                    }).catch(err=>{
                        console.log(err.response)
                        this.$swal({
                            title: '',
                            type: 'error',
                            text: '!'
                        })
                    }).finally(()=>{
                        this.isloading = false
                    })
                }
            })
        }
    }
}
</script>