<template>
<div>
    <Breadcrumbs main="seats" title="Show Record"/>
    <loading
    loader="dots"
    :active.sync="loading_state"
    :can-cancel="false"
    :is-full-page="true"
    ></loading>
    <div class="container-fluid">
        <div class="card">
            <div class="card-header">
                <p class="h4 txt-primary"></p>
                <span></span>
            </div>
            <div class="card-body">
                <b-row>
                    <b-col>
                        <p class="h6 txt-danger"><label class="txt-dark">:</label> {{ record_data_info.seat_name }}</p>
                    </b-col>
                    <b-col>
                        <p class="h6 txt-danger"><label class="txt-dark">:</label> {{ type_map[record_data_info.type] }}</p>
                    </b-col>
                    <b-col>
                        <p class="h6 txt-danger"><label class="txt-dark">:</label> {{ record_data_info.time }}</p>
                    </b-col>
                    <b-col>
                        <p class="h6 txt-danger"><label class="txt-dark">:</label> {{ record_data_info.count }}</p>
                    </b-col>
                </b-row>
            </div>
        </div>
        <b-row align-v="stretch">
            <b-col>
                <div class="card">
                    <div class="card-header">
                        <p class="h5 txt-success"></p>
                        <span></span>
                    </div>
                    <div class="card-body my-gallery" align="center">
                    <div class="col" itemscope itemprop="thumbnail" align='center'>
                        <b-row>
                            <b-col md="7">
                                <!-- <a>
                                    <img :src="getpath()" class="img-thumbnail" alt="Image description" />
                                </a> -->
                                <table class="table" id="dtable" style="border-collapse: collapse;">
                                    <tbody>
                                        <tr v-for="row in rows" :key="row" >
                                            <td v-bind:style="{'background-color': color(col), 'border': '1px solid #333'}" v-for="col in row" :key="col">{{ }}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </b-col>
                            <b-col align='left' align-self="end">
                                <div class="caption">
                                    <h5 v-text="`: ${posture_map[select_record.posture]}`"></h5>
                                    <p v-text="`: ${select_record.time}`"></p>
                                </div>
                            </b-col>
                        </b-row>
                        <b-row align-v="center">
                            <label class="col-md-2 control-label sm-left-text" for="u-range-06"></label>
                            <div class="col-md-9 col-11">
                                <VueSlideBar v-model="slider.value" :data="slider.data" :range="slider.range" class="month-range"></VueSlideBar>
                            </div>
                        </b-row>
                    </div>
                    <br/>
                    </div>
                </div>
            </b-col>
            <b-col>
                <div class="card">
                    <div class="card-header">
                        <p class="h5 txt-success"></p>
                        <span></span>
                    </div>
                    <div class="card-body chart-pie">
                     <GChart
                      class="chart-pie" 
                      id="pie-chart2"
                      type="PieChart"
                      :data="pie_chart.chartData"
                      :options="pie_chart.options"
                    />
                  </div>
                </div>
            </b-col>
        </b-row>
        <div class="card">
            <div class="card-header">
                <h5></h5>
            </div>
            <div class="card-body">
                <div class="datatable-vue m-0">
                    <h6></h6>
                    <div class="row filter-smart">
                        <div class="col-sm-2">
                            <input class="form-control" v-model="filters.time.value" placeholder=""/>
                        </div>
                        <div class="col-sm-2">
                            <b-form-select class="form-control" v-model="filters.posture.value" :options="posture_options" placeholder="" />
                        </div>
                    </div>
                    <div class='table-responsive vue-smart'>
                        <v-table
                            :data="records_list" class="table"
                            :currentPage.sync="filter.currentPage"
                            :pageSize="10"
                            @totalPagesChanged="filter.totalPages = $event"
                            :filters="filters">
                        <thead slot="head" align="center">
                            <v-th sortKey="time"></v-th>
                            <v-th sortKey="data"></v-th>
                            <v-th sortKey="posture"></v-th>
                            <v-th sortKey="id"></v-th>
                        </thead>
                        <tbody slot="body" slot-scope="{displayData}" align='center'>
                            <tr v-for="row in displayData" :key="row.id">
                                <td>{{ row.time }}</td>
                                <td>{{ row.data }}</td>
                                <td>{{ posture_map[row.posture] }}</td>
                                <td>
                                    <!-- <label>{{ row.id }}</label> -->
                                    <b-button class="btn btn-danger btn-xs m-1" @click="delete_record(row)" ><i class="icofont icofont-trash"></i></b-button>
                                </td>
                            </tr>
                        </tbody>
                    </v-table>
                    </div>
                    <smart-pagination
                    :currentPage.sync="filter.currentPage"
                    :totalPages="filter.totalPages"
                    />
                </div> 
            </div>
        </div>
    </div>
</div>
</template>

<script>
import { GChart } from 'vue-google-charts'
import VueSlideBar from 'vue-slide-bar'
import { api_server } from '@/main'
export default {
    components: { GChart, VueSlideBar },
    created(){
        this.get_record_data()
        // this.adjust_pie_chart()
    },
    // mounted(){
    //     let tb_width = document.getElementById('dtable').rows[0].offsetWidth
    //     this.observer = new MutationObserver((mutations) =>{
    //         mutations.forEach(mutation =>{
    //             if (mutation){
    //                 console.log("tb changed")
    //                 this.settableheight('dtable')
    //                 this.adjust_pie_chart()
    //             }
    //         });
    //     });
    //     this.observer.observe(tb_width, {childList: true, characterData: true, attributes: true})
    // },
    // beforeDestroy(){
    //     this.observer.disconnect()
    // },
    computed:{
        loading_state: function(){
            return this.$store.state.isLoading
        },
        selected_slider: function(){
            return this.slider.value
        }
    },
    watch:{
        selected_slider: function(){
            this.select_record = this.records_slider[this.slider.value]
            this.rows = this.reshape_rows(this.select_record.data)
            this.settableheight('dtable')
            this.adjust_pie_chart()
        }
    },
    data(){
        return {
            observer: null,
            rows: [
                // [10, 20, 40, 60, 34],
                // [102, 62, 83, 19, 44],
                // [1, 9, 11, 30, 72],
                // [1, 9, 11, 30, 99],
                // [1, 9, 11, 107, 72],
            ],
            record_data_info:{
                seat_id: this.$route.params.id,
                // seat_name: "test seat",
                // type: "2",
                // time: "2021-12-12 08:12:34~08:17:17",
                // count: 36
            },
            select_record:{
                // data_id:0 ,time:"2021-12-12 08:12:34", data:[], posture:"bias_right"
            },
            records_list:[
                // {data_id:0, time:"08:12:34", data:[187, 356, 388, 142, 591, 862, 831, 431, 444, 980, 974, 660, 591, 783, 989, 599, 352, 721, 789, 671, 320, 623, 689, 311], posture:"regular"},
                // {data_id:3, time:"08:17:17", data:[], posture:"stand_on"}
            ],
            records_slider:{
                // "08:12:34": {data_id:0, time:"2021-12-12 08:12:34", data:[], posture:"regular"},
                // "08:17:17": {data_id:3, time:"2021-12-12 08:17:17", data:[], posture:"stand_on"},
            },
            pie_chart: {
                chartData: [
                    // ['Task', 'Hours per Day'],
                    // ['',     5],
                    // ['',      10],
                    // ['',  15],
                    // ['', 20],
                    // ['',    25],
                    // ['', 25]
                ],
                options: { 
                    title: '',
                    is3D: true,
                    width:'100%',
                    height: 1,
                    colors: ["#4466f2", "#1ea6ec", "#22af47", "#007bff", '#B87070', "#FF5370"]
                },
            },
            slider:{
				// value:"08:12:34",
				// data:[
                //     "08:12:34",
                //     "08:17:17"
				// ],
				// range:[
                //     {
                //         label: "08:12"
                //     },
                //     {
                //         label: "08:13"
                //     },
                //     {
                //         label: "08:14"
                //     },
                //     {
                //         label: "08:15"
                //     },
                //     {
                //         label: "08:17"
                //     },
				// ]
			},
            posture_map: {
                '': "",
                regular: "",
                bias_left:"",
                bias_right:"",
                cross_left:"",
                cross_right:"",
                stand_on:"",
                '0': '',
                '1': '',
                '2': '',
                '3': '',
                '4': '',
                '5': '',
                '6': '',
                '7': '',
                '8': ''
            },
            posture_options:[{value: '', text: ""},
                             {value: 'regular', text: ""},
                             {value: 'bias_left', text: ""},
                             {value: 'bias_right', text: ""},
                             {value: 'cross_left', text: ""},
                             {value: 'cross_right', text: ''},
                             {value: 'stand_on', text: ""},
                             {value: '0', text: ''},
                             {value: '1', text: ''},
                             {value: '2', text: ''},
                             {value: '3', text: ''},
                             {value: '4', text: ''},
                             {value: '5', text: ''},
                             {value: '6', text: ''},
                             {value: '7', text: ''},
                             {value: '8', text: ''}],

            filter:{
				currentPage: 1,
				totalPages: 0,
			},
            filters:{
                time: { value: '', keys: ['time'] },
                posture: { value: '', keys: ['posture'] },
            },
            type_map:{
                1:'A-0001',
                2:'C-8763'
            }
        }
    },
    methods:{
        color: function(col) {
            const scale = 0.499
            if (col <= 512){
                if (col <= 0) return `#00FF00`
                return `#${parseInt(col *scale).toString(16).padStart(2, '0')}FF00`.toUpperCase()
            }else{
                if (col >= 1026) return `#FF0000`
                var diff = 255 - parseInt((col-512)*scale)
                return `#FF${diff.toString(16).padStart(2, '0')}00`.toUpperCase()
            }
        },
        reshape_rows: function(data) {
            var data_arr = data.slice(1,data.length-1).split(',').map(i => parseInt(i))
            var n_data = []
            while(data_arr.length) n_data.push(data_arr.splice(0,5))
            return n_data
        },
        settableheight: function(id) {
            var tb = document.getElementById(id)
            tb.style.height = tb.rows[0].cells[0].offsetWidth * tb.rows.length + 'px'
        },
        setimagesparth() {
            for (var i = 0; i < this.biglightgallery.length; i++) {
                this.biglightgallery[i] = this.getbigimgpath(this.biglightgallery[i]);
            }
        },
        getpath() {
            let seat_type = (this.record_data_info.type > 2) ? 1 : this.record_data_info.type 
            let filename = `${seat_type}_${this.select_record.posture}.png` 
            return require("@/assets/images/smart_seat_img/" + filename);
        },
        adjust_pie_chart(){
            // this.pie_chart.options.height = (this.record_data_info.type == 1) ? 494 : 494
            this.pie_chart.options.height = document.getElementById('dtable').rows[0].offsetHeight*5 + 100
        },
        get_record_data(){
            let data_type = this.$route.params.data_type
            let seat_id = this.$route.params.id
            let search_date = this.$route.params.date
            this.$store.commit("change_loading_state", true)
            api_server.get(`/seat/get-data/${data_type}/${seat_id}/${search_date}`)
            .then(res =>{
                if (res.data.message == ''){
                    this.$swal({
                        title:'',
                        type: 'info',
                        text:''
                    })
                    this.$router.push(`/sensor/records/${seat_id}`)
                }
                else{
                    this.record_data_info = res.data.data.record_data_info
                    this.records_slider = res.data.data.records_slider
                    this.records_list = res.data.data.records_list
                    this.slider = res.data.data.slider
                    this.pie_chart.chartData = res.data.data.chartData
                    this.type_map = res.data.data.type_map
                }
            }).catch(err =>{
                this.$swal({
                    title:'!',
                    type: 'error',
                    text: err.response.message
                })
            }).finally(() =>{
                this.settableheight('dtable')
                this.adjust_pie_chart()
                this.$store.commit("change_loading_state", false)
            })
        },
        delete_record(row_data){
            this.$swal({
                type:'warning',
                title:"",
                text: `${row_data.time}?`,
				showCancelButton: true,
				confirmButtonText: '',
				confirmButtonColor: '#4466f2',
				cancelButtonText: '',
				cancelButtonColor: '#efefef',
				reverseButtons: true
			}).then(result => {
                if(result.value){
                    this.$store.commit('change_loading_state', true)
                    api_server.delete(`/seat/delete-data`, {params: {mode:'s', data_id: row_data.data_id}})
                        .then(res =>{
                        if (res.data.status == 'success'){
                            this.$toasted.show('!',{
                                theme: 'bubble',
                                position: "top-right",
                                type: 'success',
                                duration: 2000
                            })
                            this.get_record_data()
                        } else {
                            throw res.data.message
                        }
                    }).catch(err =>{
                        this.$swal({
                            title: "",
                            type: 'error',
                            text: err.response.data.message
                        })
                    }).finally(() =>{
                    })
                }
            })
        }
    }
}
</script>